.parallax-header {
	h1 {
		> span {
			overflow: hidden;

			@for $i from 1 through 60 {
				&:nth-child(#{$i}) span {
					animation-delay: (#{0.6 + ($i * 0.12)}s);
				}
			}
		}

		> span > span {
			animation: ghostIn $duration-long $easing1;
			animation-fill-mode: both;
		}
	}
}



section {
	img {
		opacity: 0;
		transition: none;
		transform: translate3d(0,120px,0);
	}

	blockquote {
		transition: none;
		opacity: 0;
	}

	.grid__item  .grid__item,
	.grid__item {
		@for $i from 1 through 60 {
			&:nth-child(#{$i}) {
				img, video {
					transition-delay: (#{0.9 + ($i * 0.3)}s);
				}
				blockquote {
					transition-delay: (#{0.3 + ($i * 0.3)}s);
				}
			}
		}
	}

	.split--word, .split--letter {
		overflow: hidden;

		> span {
			transition: none;
			opacity: 0;
		}
	}

	h2, h3, h4, h5, h6 {
		transition: none;
		opacity: 0;

		.split--word, .split--letter {
			> span {
				transform: translate3d(0,90px,0) skewX(9deg);
			}
		}
	}

	p, blockquote {
		span > span{
			opacity: 0;
			transform: translate3d(0,100%,0) skewX(6deg);
		}
	}
}

.active-section {
	section {
		h2, h3, h4, h5, h6 {
			opacity: 1;
			transition: transform $duration-long $delay-medium $easing1, opacity $duration-long $delay-medium $easing1;

			.split--word, .split--letter {
				> span {
					transition: transform $duration-long $easing1, opacity $duration-long $easing1;
					opacity: 1;
					transform: none;
				}
			} 
		}

		h2, h3 {
			transition-delay: $delay-short;
		}

		h4 {
			transition-delay: $delay-longer;
		}

		h5, h6 {
			transition-delay: $delay-longest;
		}


		h2, h3, h4, h5, h6 {
			.split--word {
				@for $i from 1 through 60 {
					&:nth-child(#{$i}) {
						span {
							transition-delay: (#{0.6 + ($i * 0.18)}s);							
						}
					}
				}
			}
			.split--letter > span {
				@for $i from 1 through 60 {
					&:nth-child(#{$i}) {
						transition-delay: (#{0.6 + ($i * 0.09)}s);							
					}
				}
			}
		}

		img, video {
			transition: transform $duration-long $easing2, opacity $duration-long $easing2;
			opacity: 1;
			transform: translate3d(0,0,0) skewX(0deg);
		}

		p, blockquote {
			opacity: 1;
			transition: transform $duration-long $easing1, opacity $duration-long $easing1;

			.split--word {
				@for $i from 1 through 120 {
					&:nth-child(#{$i}) span {
						transition-delay: (#{1.2 + ($i * 0.03)}s);
					}
				}
			}

			.split--letter > span {
				@for $i from 1 through 120 {
					&:nth-child(#{$i}) {
						transition-delay: (#{1.2 + ($i * 0.03)}s);
					}
				}
			}

			.split--word, .split--letter {
				> span {
					opacity: 1;
					transform: translate3d(0,0,0) skewX(0deg);
					transition: all $duration-medium $easing1;
				}	
			} 
		}
	}
}


